import React from 'react';

import './controlboard.css';
import Element from './element.js';

class ControlBoard extends React.Component{
    constructor(props){
        super(props);
        this.state = {
        }
    }

    myLog(e){
        console.log('mylog ->',e)
    }

    render(){
        return(
            <div className="control-board-wrapper">
                <div className="control-board-element-explain-wrapper">
                    <div className="current-element-wrapper">
                        <Element
                            metaData={this.props.currentElemData}
                            bottomData={this.props.currentbottomData}
                            elemClass={'current-element'}
                        />
                    </div>
                    <img className="img-line" src={require('../../file/lines.svg')}></img>
                    <div className="element-explain">原子序</div>
                    <div className="element-explain">符号</div>
                    <div className="element-explain">中文名</div>
                    <select name="bottomDataSelect" defaultValue="a" onChange={this.props.handleSelectChange} className="element-explain">
                        <option value="atomic_mass">原子量</option>
                        <option value="en">英文名</option>
                        <option value="pinyin">拼音</option>
                    </select>
                </div>
                <div className="control-board-tag-wrapper">
                    <span>STP 标准状况下</span>
                    <div className="control-board-tag-group">
                        <div className={"control-board-tag " + (this.props.currentElemTag.metal ? 'tag' : '')}>金属</div>
                        <div className={"control-board-tag " + (this.props.currentElemTag.metal ? '' : 'tag')}>非金属</div>
                        <div className={"control-board-tag " + (this.props.currentElemTag.liquid ? 'tag' : '')}>液态</div>
                        <div className={"control-board-tag " + (this.props.currentElemTag.solid ? 'tag' : '')}>固态</div>
                        <div className={"control-board-tag " + (this.props.currentElemTag.gas ? 'tag' : '')}>气态</div>
                        <div className={"control-board-tag " + (this.props.currentElemTag.ukPhase ? 'tag' : '')}>未知相态</div>
                    </div>
                    <div className="control-board-tag-group">
                        <span>半衰期</span>
                        <div className={"control-board-tag " + (this.props.currentElemTag.Radioactivity ? 'tag' : '')}>放射性</div>
                    </div>
                </div>
            </div>
        )
    }
}


export default ControlBoard;