import React from 'react';
import './element.css';

class Element extends React.Component{
    constructor(props){
        super(props);
        this.state={
            meclass:'',
            mostyle:{},
        };
        this.color = {
            'h':'#e9db39',
            'he': '#4e1892',    
            'li': '#dc143c',
            'be': '#2578b5',
            'b': '#cc3536',
            'al': '#0041a5',
            'f': '#005b5a',
            //'sc': '#ce9335', // 旧 old
            'sc': '#ff9c00',  // 新配色 new color
            'la': '#a03e28',
            'ac': '#6a6834',
            'ubu': '#fff',
        }
    }

    handleMouseOver(e){
        // console.log('handleMouseOver',e);
        this.setState({
            mostyle:{'boxShadow':`0 0 20px ${this.color[this.props.metaData[4]]}`,'transition':'all 0.1s'}
        })
        if(this.props.setCurrentElem){
            this.props.setCurrentElem(this.props.metaData[0])
        }
    }

    handleMouseOut(e){
        // console.log('handleMouseOut',e);
        this.setState({
            mostyle:{'boxShadow':'0 0 0 #ffffff','transition':'all 0.5s'}
        })
    }

    signColor(symbol){
        // console.log(this.props)
        let gradient = `-webkit-gradient(linear, 100% 0%, 0% 0%,from(transparent), to(${this.color[symbol]})`
        let style = {'background':gradient};
        // let style = {'background-color':'#6a6834'}
        return style;
    }



    render(){
        if(this.props.metaData){
            let number = this.props.metaData[0];
            const elem = (
                <div className={"e-element " + this.state.meclass + this.props.elemClass}
                style={this.state.mostyle}
                onMouseOver={(e)=>this.handleMouseOver(e)}
                onMouseOut={(e)=>this.handleMouseOut(e)}  
                >
                    <span className="e-number" style={this.signColor(this.props.metaData[4])}>{number}</span>
                    <span className="e-symbol">{this.props.metaData[1]}</span>
                    <span className="e-name">{this.props.metaData[2]}</span>
                    <span className="e-bottomdata">{(this.props.bottomData)?this.props.bottomData:this.props.metaData[3]}</span>
                </div>
            )
            return elem
        }else{
            const elem = (
                <div className="e-element"/>
            )
            return elem
        }

        
    }
}

export default Element;
// module.exports = Element;