function groupBar(){
    const groupList = ["I A", "II A", "III B", "IV B", "V B", "VI B", "VII B", "VIII", "VIII", "VIII", "I B", "II B", "III A", "IV A", "V A", "VI A", "VII A", "0"];
    let row = []
    for(let i=0; i<18; i++){
        row.push(
            <div className="t-group">{groupList[i]}</div>
        )
    }
    return(
        <div className="group-bar">
            {row}
        </div>
    )
}

function periodicBar(){
    const periodicList = [1,2,3,4,5,6,7,8,null,null,];
    let column = [];
    for(let i in periodicList){
        column.push(
            <div className="t-periodic">{periodicList[i]}</div>
        )
    }
    return(
        <div className="periodic-bar">
            {column}
        </div>
    )
}

