// electron shells for canvas
var ctx;
var raf;
var canvas;


var electronShell = {
    cx : 100,
    cy : 100,
    cdr : 6,     // 中心点半径
    er : 2,       // 电子半径
    srd : 10,     // 电子层轨道半径差

    list : [],    // 每层电子数
    paa : 0*Math.PI,  //默认极轴角 default Polar axis angle
    ctx : undefined,
    ia : 0.01,      // 动画角度增量
    na : 0,        // 当前动画帧角度
    sia : 0.0005,      // 层角度增量, 外层速度大
    sna : 0,        // 当前动画帧层角度增量累积

    
    init:function(ctx,list){
        this.list = list;
        this.ctx = ctx;
        this.na = 0;
        this.sna = 0;
    },

    draw:function(){
        // 绘制中心点
        this.ctx.beginPath();
        this.ctx.arc(this.cx,this.cy,this.cdr,0,2*Math.PI,true);
        this.ctx.fill()

        for(let i =0; i < this.list.length; i++){
            // 绘制电子层轨道
            let shellRadius = this.cdr + this.srd * (i+1) + 0;
            this.ctx.beginPath();
            this.ctx.arc(this.cx,this.cy,shellRadius,0,2*Math.PI,true);
            this.ctx.strokeStyle = '#888';
            this.ctx.stroke()
            // 绘制电子
            for(let j=0; j < this.list[i]; j++){
                this.ctx.beginPath()
                let x = shellRadius * Math.cos(this.paa + 2*Math.PI/this.list[i]*j + this.na + this.sna*i) + this.cx;
                let y = shellRadius * Math.sin(this.paa + 2*Math.PI/this.list[i]*j + this.na + this.sna*i) + this.cy;
                this.ctx.arc(x,y,this.er,0,2*Math.PI,true);
                this.ctx.fill();
            }

        }
        this.na = this.na + this.ia // 动画增量
        this.sna = this.sna + this.sia //层增量
    }

}

function drawShells(id,list){
    canvas = document.getElementById(id);
    ctx = canvas.getContext('2d');
    electronShell.init(ctx, list)

    window.cancelAnimationFrame(raf)
    raf = window.requestAnimationFrame(draw)

}

function draw(){
    ctx.clearRect(0,0,canvas.width,canvas.height);
    electronShell.draw()
    raf = window.requestAnimationFrame(draw);
}

module.exports = drawShells;